.clients {
  margin-bottom: 150px;
  overflow: hidden;

  @media (max-width: $big-tablet-size) {
    margin-bottom: 100px;
  }

  @media (max-width: $mini-tablet-size) {
    margin-bottom: 70px;
  }
}

.client {
  display: flex;
  align-items: center;
}

.clients__title {
  margin-bottom: 70px;
  max-width: 318px;

  @media (max-width: $big-tablet-size) {
    margin-bottom: 45px;
    max-width: 400px;
  }
}

.clients__list {
  display: flex;
  gap: 60px;
  position: absolute;
  top: 0;
  will-change: transform;
}

.clients__slider {
  height: 160px;
  position: relative;
}

.client__img {
  min-width: 172px;

  @media (max-width: $big-laptop-size) {
    min-width: 150px;
  }

  @media (max-width: $big-tablet-size) {
    min-width: 130px;
  }

  @media (max-width: $big-phone-size) {
    min-width: 100px;
  }
}
