.non-required-container {
  display: flex;
  flex-direction: column;
}

.non-required-container__title {
  margin-bottom: 20px;
}

.non-required-container__select {
  &:first-of-type {
    margin-bottom: 15px;
  }
  border-radius: $input-border-radius;
  padding: 15px 20px;
  font-family: Roboto;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 150%; /* 24px */
  color: $input-placeholder-color;
  appearance: none; /* Скрыть стандартный стиль */
  -webkit-appearance: none;
  -moz-appearance: none;
  width: 100%;
}
