.director-form__container {
    display: flex;
    flex-direction: column;
    gap: 15px;
}

.director-form {
    padding: 90px 30px 56px !important;
    border-radius: $primary-button-border-radius;
    background-color: #f3efde !important;
    width: 66vw !important;
    max-width: none !important;

    .form__button-submit {
        width: 100%;
        padding: 21px 0;
    }
}

.form__label {
    margin-bottom: 20px;
    display: block;
}

.form__head {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

textarea {
    min-height: 112px;
}

.form__fieldset {
    margin-bottom: 20px;
}

.form__column {
    display: flex;
    flex-direction: column;
    gap: 30px;
}

.director-form__form {
    display: flex;
    flex-direction: column;
    gap: 25px;
}

.director-form__textarea{
    min-height: 150px;
}

.director-form__container {
    width: calc(100% - 280px);
}