.form-container {
  width: 100%;
  padding-top: 56px;
  padding-bottom: 56px;
  display: flex;
  flex-direction: column;
  position: relative;
  overflow: hidden;
  margin-bottom: 150px;

  @media (max-width: $desktop-size) {
    margin-bottom: 100px;
  }

  @media (max-width: $laptop-size) {
    margin-bottom: 50px;
  }

  &.step-2 {
    .form-container__button {
      display: none;
    }
    &::before {
      opacity: 0;
      transition: $normal-transition;
    }

    &::after {
      opacity: 1;
      transition: $normal-transition;
    }
  }

  @media (max-width: $phone-size) {
    min-height: 0;
    height: auto;
  }

  &::before {
    @include default-pseudo-element(270px, 100%);
    background: url('./../../../public/img/backgrounds/form-bg.svg') no-repeat
      top center;
    top: 0;
    right: 0;
    transition: $normal-transition;
    z-index: 1;

    @media (max-width: $mini-desktop-size) {
      right: -90px;
    }

    @media (max-width: $big-laptop-size) {
      top: 30px;
      z-index: 0;
    }

    @media (max-width: $big-laptop-size) {
      top: 30px;
    }

    @media (max-width: $big-phone-size) {
      display: none;
    }
  }

  &::after {
    @include default-pseudo-element(270px, 100%);
    background: url('./../../../public/img/backgrounds/form-2-bg.png') no-repeat
      top center;
    top: 0;
    right: 0;
    opacity: 0;
    transition: $normal-transition;
    z-index: 1;

    @media (max-width: $tablet-size) {
      display: none;
    }
  }
}

.form-container__form {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  z-index: 2;
  gap: 15px;
  max-width: 100%;
}

.form-container__title {
  max-width: 575px;
  margin-bottom: 20px;
  z-index: 2;
}

.form-container__text {
  flex-grow: 1;
  z-index: 2;
}

.form-container__general-info {
  max-width: 605px;
  margin-bottom: auto;
  transition: $normal-transition;

  &.hide {
    opacity: 0;
    max-height: 0;
    pointer-events: none;
    transition: $normal-transition;
    margin-bottom: 0;
  }

  & .form-container__text {
    margin-bottom: 20px;
  }

  @media (max-width: $phone-size) {
    margin-bottom: 20px;
  }
}

.form-container__general-info-container {
  display: grid;
  grid-template-columns: 45% 45%;
  gap: 15px 0;
  justify-content: space-around;

  @media (max-width: 1120px) {
    max-width: 605px;
  }

  @media (max-width: 700px) {
    max-width: 87vw;
  }

  @media (max-width: $big-phone-size) {
    grid-template-columns: 100%;
    max-width: 85vw;
  }
}

.form-container__button-container {
  display: flex;
  gap: 35px;
  align-items: center;
  justify-content: center;
  width: 100%;

  @media (max-width: $phone-size) {
    justify-content: space-between;
  }
}

.form-container__button {
  width: 285px;
}

.form-container__non-required {
  opacity: 0;
  max-height: 0;
  pointer-events: none;
  z-index: 2;
  position: relative;
  transition: $normal-transition;
  margin-bottom: 0;

  &.active {
    display: grid;
    gap: 40px;
    grid-template-columns: 2.2fr 3.3fr;
    max-height: none;
    pointer-events: all;
    opacity: 1;
    transition: $normal-transition;
    margin-bottom: auto;

    @media (max-width: $big-phone-size) {
      grid-template-columns: 1fr;
    }
  }
}

.form-container__comment-label {
  max-width: 442px;
}

.form-container__comment {
  width: 100%;
  flex-grow: 1;
  width: 37.34vw;

  @media (max-width: $tablet-size) {
    width: auto;
    height: 30vh;
  }
}

.form-container__comment-container {
  display: flex;
  flex-direction: column;
  gap: 14px;
}

.form-container__input-container {
  display: flex;
  flex-direction: column;
}

.form__grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 15px 35px;

  @media (max-width: 1439px) {
    gap: 15px 20px;
  }

  @media (max-width: $mini-desktop-size) {
    grid-template-columns: 100%;
    gap: 15px 0;
  }
}