.statistic {
  height: 172px;
  justify-content: space-between;
  display: flex;
  flex-direction: column;
  padding: 20px;

  @media (max-width: $tablet-size) {
    height: 128px;
  }

  @media (max-width: $mini-desktop-size) {
    padding: 10px;
    height: 108px;
  }
}

.statistic__text {
  color: $special-text-color;
}
