.container-counter {
  margin-bottom: 15px;
  display: flex;
  gap: 10px;
  align-items: center;
}

.container-counter__button {
  min-width: 51px;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: $secondary-text-color;
  border-radius: $input-border-radius;

  @media (max-width: $tablet-size) {
    height: 40px;
    min-width: 40px;
  }
}

.container-counter__input {
  height: 60px;
  width: 69px;
  border: none;
  text-align: center;

  @media (max-width: $tablet-size) {
    height: 40px;
    width: 60px;
  }
}
