.page-menu__list {
  display: flex;
  gap: 50px;

  @media (max-width: $big-phone-size) {
    gap: 30px;
  }
}

.page-menu__button {
  color: $primary-text-color;
  text-transform: uppercase;
  &.active {
    color: $special-text-color;
  }
}

.page-menu {
  margin-bottom: 70px;

  @media (max-width: $big-tablet-size) {
    margin-bottom: 45px;
  }
}
