.article {
  display: flex;
  gap: 30px;
  align-items: center;

  &:hover {
    .article__title {
      transition: $fast-transition;
      color: $special-text-color;
    }
  }
}

.article__title {
  transition: $fast-transition;
}

.article__img-container {
  min-width: 80px;
  max-width: 80px;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: $primary-dark-bg;
}

.article__img {
  width: 100%;
  height: auto;
}

