.principles__img {
  border-radius: $image-border-radius;
  position: sticky;
  top: 77px;

  // @media (max-width: $phone-size) {
  //   position: static;
  // }
}

.principles {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 34px;
  position: relative;
  margin-bottom: 130px;

  @media (max-width: $big-tablet-size) {
    margin-bottom: 90px;
  }

  @media (max-width: $mini-tablet-size) {
    gap: 25px;
  }

  @media (max-width: $big-phone-size) {
    gap: 20px;
  }

  @media (max-width: $phone-size) {
    grid-template-columns: 1fr;
  }
}

.principles__title {
  margin-bottom: 40px;

  @media (max-width: $big-tablet-size) {
    margin-bottom: 30px;
  }

  @media (max-width: $mini-tablet-size) {
    margin-bottom: 20px;
  }
}

.principles__text {
  max-width: 80%;
  &:first-of-type {
    margin-bottom: 10px;
  }

  @media (max-width: $laptop-size) {
    max-width: 95%;
  }
}

.principles__statistics {
  margin-top: 106px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;

  @media (max-width: $big-tablet-size) {
    margin-top: 60px;
    gap: 25px;
  }

  @media (max-width: $tablet-size) {
    gap: 15px;
  }

  @media (max-width: $mini-tablet-size) {
    margin-top: 40px;
  }

  @media (max-width: $big-phone-size) {
    gap: 10px;
    margin-top: 30px;
  }
}
