.benefits {
  margin-bottom: 150px;

  @media (max-width: $big-tablet-size) {
    margin-bottom: 100px;
  }
}

.benefits__title {
  margin-bottom: 70px;
  max-width: 358px;

  @media (max-width: $big-tablet-size) {
    margin-bottom: 45px;
  }
}

.benefits__list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;

  @media (max-width: $big-tablet-size) {
    gap: 25px;
  }

  @media (max-width: $big-phone-size) {
    gap: 15px;
  }

  @media (min-width: $phone-size) {
    display: grid !important;
  }
}
