.services {
  margin-bottom: 150px;

  @media (max-width: $big-tablet-size) {
    margin-bottom: 100px;
  }
}

.services__title {
  margin-bottom: 70px;

  @media (max-width: $big-tablet-size) {
    margin-bottom: 50px;
  }

  @media (max-width: $big-phone-size) {
    margin-bottom: 30px;
  }
}

.services__list {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
