.service {
  display: flex;
  align-items: center;
  gap: 20px;
  transition: $long-transition;
  position: relative;
  &:hover {
    transition: $long-transition;
    background-color: $primary-button-bg;
    & .service__img {
      opacity: 1;
      transition: $normal-transition;
      right: 50px;
      @include centering-vertical-pseudo-element;
      top: 30%;

      @media (max-width: $laptop-size) {
        right: 30px;
      }

      @media (max-width: $big-tablet-size) {
        right: 10px;
      }
    }

    & .service__title {
      transition: $normal-transition;
      color: $secondary-text-color;
    }
    .service__icon {
      opacity: 1;
      transition: $normal-transition;
      transform: translateX(0);
    }
  }
}

.service__img {
  transition: $normal-transition;
  position: absolute;
  opacity: 0;
  pointer-events: none;
  width: 360px;
  height: auto;
  top: -20px;
  right: -40px;
  z-index: 10;

  @media (max-width: $big-laptop-size) {
    width: 350px;
  }
  @media (max-width: $laptop-size) {
    width: 300px;
    right: -50px;
  }

  @media (max-width: $big-tablet-size) {
    width: 250px;
    right: -70px;
  }

  @media (max-width: $tablet-size) {
    width: 210px;
  }

  @media (max-width: $mini-tablet-size) {
    width: 160px;
  }
}

.service__title {
  transition: $normal-transition;
}

.service__icon {
  opacity: 0;
  transition: $normal-transition;
  transform: translateX(-30px);
  padding-top: 5px;
}
