.thanks-letter {
  height: 23.77vw !important;
  max-height: 350px;
  width: 100%;

  // Эффект переворота
  perspective: 1000px;
  position: relative;

  @media (max-width: $big-tablet-size) {
    height: 45vw !important;
  }

  @media (max-width: $tablet-size) {
    height: 46vw !important;
  }

  @media (max-width: $phone-size) {
    height: 230px !important;
  }
}

.thanks-letter__logo {
  width: 80px;
  height: 80px;
}

.thanks-letter__letter {
  width: 100%;
  height: 100%;
  border-radius: $primary-border-radius;
}

.flip-card {
  padding: 40px 30px;
  background-color: #e5ddc9;
  border-radius: $primary-border-radius;
  width: 100%;
  height: 100%;
  position: relative;
  transform-style: preserve-3d;
  transition: transform $normal-transition;

  @media (max-width: $laptop-size) {
    padding: 30px 20px;
  }

  @media (max-width: $phone-size) {
    padding: 25px 10px;
  }
}

.flip-container:hover .flip-card {
  transform: rotateY(180deg);
}

.flip-card-front,
.flip-card-back {
  width: 100%;
  height: 100%;
  position: absolute;
  backface-visibility: hidden;
}

.flip-card-front {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  @include centering-pseudo-element;
  padding: 40px 30px;

  @media (max-width: $laptop-size) {
    padding: 30px 20px;
  }

  @media (max-width: $phone-size) {
    padding: 25px 10px;
  }
}

.flip-card-back {
  display: flex;
  align-items: center;
  justify-content: center;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotateY(180deg);
}
