.articles {
  margin-bottom: 40px;
}

.articles__container {
  margin-bottom: 70px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;

  @media (max-width: $mini-desktop-size) {
    grid-template-columns: 1fr;
    gap: 20px;
  }
}

.articles__title {
  max-width: 425px;
}

.articles__side-container {
  padding: 40px 20px;
  display: flex;
  flex-direction: column;
  gap: 20px;
  border: 2px solid $secondary-border-color;
  border-radius: $primary-border-radius;
}
